import React, { useState } from 'react';
import { Button, Carousel, Col, Container, Row } from 'react-bootstrap';
import one from '../../images/one.jpg'
// import two from '../../images/two.jpg'
// import three from '../../images/three.jpg'

function Corousel() { 
    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    return(
        <>
            <Carousel activeIndex={index} onSelect={handleSelect}>
                <Carousel.Item>
                    <Container fluid >
                        <Row>
                            <Col md={6} className="d-flex justify-content-center">
                                <img
                                className="bannerImageWidth"
                                src={one}
                                alt="First slide"
                                />
                            </Col>
                            <Col md={6} className="text-center">
                                <h1 className="brownColor mt-2 openSansSemiBold">Dry Coconuts</h1>
                                <h5 className="pt-3 openSansRegular">We are one of the leading exporters of dry coconuts</h5>
                                <p className="pt-2 openSansRegular">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic</p>
                                <Button variant="outline-dark" className="m-1 openSansSemiBold">Get a Quote Now</Button>
                            </Col>
                        </Row>
                    </Container>

                </Carousel.Item>
                {/* <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src={two}
                    alt="Second slide"
                    />

                    <Carousel.Caption>
                    <h3 className="openSansSemiBold">Second slide label</h3>
                    <p className="openSansSemiBold">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src={three}
                    alt="Third slide"
                    />

                    <Carousel.Caption>
                    <h3 className="openSansSemiBold">Third slide label</h3>
                    <p className="openSansSemiBold">
                        Praesent commodo cursus magna, vel scelerisque nisl consectetur.
                    </p>
                    </Carousel.Caption>
                </Carousel.Item> */}
            </Carousel>
        </>
    )
}
export default Corousel;